import axios from 'axios'


export default async _ => {
    const url = 'http://ip-api.com/json/?fields=status,message,country,countryCode,region,regionName,city,zip,lat,lon,timezone,isp,org,as,reverse,query'

    try {
       const response = await axios.get(url)
        return response.data
    } catch (error) {
        console.log(error)
    }
}